import axios from 'axios';
import * as Config from '../config';
import store from '../store';

const Client = axios.create({ baseURL: Config.BACKEND_URL });

// Se existe JWT no store, mandar junto com qualquer solicitaÃ§Ã£o
Client.interceptors.request.use((request) => {
  if (store.getters.token) {
    request.headers['Authorization'] = `Bearer ${store.getters.token}`;
  }
  return request;
});

Client.interceptors.response.use(
  (response) => {
    // Interpretar comandos do servidor
    let serverCommand = response.headers['Server-Command'];
    if (serverCommand) {
      if (serverCommand.includes('lose-token')) {
        // Servidor mandou perder o token.
        // eslint-disable-next-line
        console.log('oi mundo, eu perdi o token :x');
        store.commit('clearToken');
      } else if (serverCommand.includes('refresh-token')) {
        // Servidor mandou atualizar o token.
        // eslint-disable-next-line
        console.log('oi mundo, eu necessito novo token :|');
        Client.get('/login/refresh')
          .then(() => {})
          .catch(() => {});
      }
    }

    // Guardar token se for recebido
    if (response.data.token) {
      // eslint-disable-next-line
      // console.log(`Veio token: ${response.data.token.token}`);
      store.commit('setToken', response.data.token.token);
    } else {
      // eslint-disable-next-line
      console.log(
        'DEBUG: NÃ£o veio token. Implemente refresh ou remova essa linha.'
      );
    }
    return response;
  },
  (error) => {
    if (error.response.status == 401) {
      // eslint-disable-next-line
      // console.log(error.response);
      // if (error.response.data.message === 'The token has expired') {
      window.getApp.$emit('APP_LOGOUT', 'VocÃª foi desconectado');
      // }
    }
    // A linha abaixo Ã© mega importante
    return Promise.reject(error);
  }
);

export { Client };
