import Vue from 'vue';
import Vuex from 'vuex';
import VuexPersist from 'vuex-persist';
import jwtdecode from 'jwt-decode';

Vue.use(Vuex);

const vuexLocalStorage = new VuexPersist({
  key: 'vuex', // The key to store the state on in the storage provider.
  storage: window.localStorage // or window.sessionStorage or localForage
});

export default new Vuex.Store({
  state: {
    token: null
  },
  mutations: {
    setToken: (state, token) => {
      state.token = token;
    },
    clearToken: (state) => {
      state.token = null;
    }
  },
  actions: {},
  getters: {
    nome: (state) => {
      if (state.token) return jwtdecode(state.token).nome;
      return null;
    },
    idUsuario: (state) => {
      if (state.token) return jwtdecode(state.token)._id;
      return null;
    },
    permissao: (state) => {
      if (state.token) return jwtdecode(state.token).permissao;
      return null;
    },
    token: (state) => {
      return state.token;
    }
  },
  plugins: [vuexLocalStorage.plugin]
});
