import { Client } from './rest-client'
import fileDownload from 'js-file-download'

export {
  upload,
  download,
  addUpload,
}

const addUpload = (arquivo, idMovimentacao) => new Promise((resolve, reject) => {
  const formData = new FormData()
  formData.append('document', arquivo)
  Client.post(`${idMovimentacao}/upload`,
    formData,
    { headers: { 'Content-Type': 'multipart/form-data' } },
  ).then(resultado => resolve(resultado.data))
   .catch(err => reject(err))
})

const upload = (arquivo) => new Promise((resolve, reject) => {
  const formData = new FormData()
  formData.append('document', arquivo)
  Client.post('/upload',
    formData,
    {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    },
  ).then(result => {
    resolve(result.data)
  }).catch(err => {
    reject(err)
  })
})

const download = (arquivo) => new Promise((resolve, reject) => {
  Client.get(`/download/${arquivo.arquivoId}`, { responseType: 'blob', })
    .then(result => {
      let blob = new Blob([result.data])
      fileDownload(blob, arquivo.nome)
      resolve(blob)
    })
    .catch(err => reject(err))
})
