<template>
  <v-toolbar color="primary" fixed dark app>
    <v-toolbar-title class="ml-0 pl-3"></v-toolbar-title>
    <v-toolbar-side-icon @click.stop="handleDrawerToggle"></v-toolbar-side-icon>

    <!--v-spacer></v-spacer>
    <span v-if="pegouCotacao">
      {{Number(1).toLocaleString("pt-BR", {style: "currency", currency: "USD"})}} =
      <b>{{cotacaoBrl.toLocaleString("pt-BR", {style: "currency", currency: "BRL"})}}</b>
    </span-->
    <v-spacer></v-spacer>

    <v-menu offset-y origin="center center" :nudge-bottom="10" transition="scale-transition">
      <v-btn flat large slot="activator">
        <span
          class="font-weight-regular text-capitalize"
          style="margin-right: 0.6em"
        >{{ user.name }}</span>
        <v-avatar icon flat size="30px">
          <!-- <img src="/static/avatar/a1.jpg" :alt="user.name"> -->
          <v-icon medium>account_circle</v-icon>
        </v-avatar>
      </v-btn>
      <v-list class="pa-0">
        <v-list-tile
          v-for="(item,index) in items"
          :to="!item.href ? { name: item.name } : null"
          :href="item.href"
          @click="item.click"
          ripple="ripple"
          :disabled="item.disabled"
          :target="item.target"
          rel="noopener"
          :key="index"
        >
          <v-list-tile-action v-if="item.icon">
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-menu>
  </v-toolbar>
</template>

<script>
import API from "@/api/";

export default {
  name: "app-toolbar",
  // components: {
  //   NotificationList
  // },
  data: () => ({
    items: [
      {
        icon: "perm_identity",
        href: "/#/cadastro",
        title: "Meu Cadastro",
        click: e => {
          e;
          // location.replace("#/cadastro");
        }
      },
      {
        icon: "fullscreen_exit",
        href: "#",
        title: "Logout",
        click: e => {
          e;
          window.getApp.$emit("APP_LOGOUT");
        }
      }
    ],
    user: {
      name: API.Auth.getNome()
    },
    cotacaoBrl: 1.0,
    pegouCotacao: false
  }),
  computed: {
    toolbarColor() {
      return this.$vuetify.options.extra.mainNav;
    }
  },

  created() {
    this.getCotacaoUSDaBRL();
  },

  methods: {
    getCotacaoUSDaBRL() {
      API.Cotacao.getCotacaoUSDaBRL((err, resultado) => {
        if (err) {
          // eslint-disable-next-line
          console.log(err);
        } else {
          this.cotacaoBrl = resultado.data.rates.BRL;
          this.pegouCotacao = true;
        }
      });
    },
    handleDrawerToggle() {
      window.getApp.$emit("APP_DRAWER_TOGGLED");
    }
    // handleFullScreen () {
    //   Util.toggleFullScreen();
    // }
  }
};
</script>
