import { Client } from './rest-client';

const prioridades = ['Baixa', 'MÃ©dia', 'Alta', 'Urgente'];

const enviarEmail = (assunto, prioridade, mensagem, callback) => {
  let body = { assunto, prioridade, mensagem };

  Client.post('/contato/enviar-email', body)
    .then((resultado) => {
      if (!resultado) {
        callback(null, false);
      } else {
        callback(null, true);
      }
    })
    .catch((err) => {
      callback(err);
    });
};

export { prioridades, enviarEmail };
