import axios from 'axios';

export { getCotacaoUSDaBRL };

const getCotacaoUSDaBRL = (callback) => {
  let api = axios.create();
  api
    .get('https://api.exchangeratesapi.io/latest?base=USD&symbols=USD,BRL')
    .then((value) => {
      // eslint-disable-next-line
      // console.log(value);
      callback(null, value);
    })
    .catch((err) => {
      // eslint-disable-next-line
      console.log(err);
      if (err.response.status === 401) {
        callback(null, false);
      } else {
        callback(err);
      }
    });
};
