<template>
  <v-layout row class="align-center layout px-4 pt-4 app--page-header">
    <!-- <div class="page-header-left">
      <h3 class="pr-3">{{ title }}</h3>
    </div>-->
    <!-- <v-breadcrumbs divider="-" :items="breadcrumbs"></v-breadcrumbs> -->
    <!-- <v-spacer></v-spacer>
    <div class="page-header-right">
      <v-btn icon @click="refresh">
        <v-icon class="text--secondary">refresh</v-icon>
      </v-btn>
    </div>-->
  </v-layout>
</template>

<script>
import API from "@/api";
export default {
  data() {
    return {
      title: ""
    };
  },
  computed: {
    breadcrumbs: function() {
      let breadcrumbs = [];
      API.Menu.menu().forEach(item => {
        if (item.items) {
          let child = item.items.find(i => {
            return i.component === this.$route.name;
          });
          if (child) {
            breadcrumbs.push(item.title);
            breadcrumbs.push(child.title);
            // eslint-disable-next-line
            this.title = child.title;
          }
        } else {
          if (item.name === this.$route.name) {
            // eslint-disable-next-line
            this.title = item.title;
            breadcrumbs.push(item.title);
          }
        }
      });
      return breadcrumbs;
    }
  },

  methods: {
    refresh() {
      location.reload();
    }
  }
};
</script>