// getCadastro
// Busca o cadastro do usuÃ¡rio utilizador.
// Para buscar o cadastro de outro usuÃ¡rio use getUsuario.
const getCadastro = (callback) => {
  let idUsuario = store.getters.idUsuario;

  Client.get(`/usuario/${idUsuario}`)
    .then((resultado) => {
      callback(null, resultado.data);
    })
    .catch((err) => {
      callback(err);
    });
};

// getUsuario
// Busca o cadastro de um usuÃ¡rio.
const getUsuario = (uid, callback) => {
  Client.get(`/usuario/${uid}`)
    .then((resultado) => {
      callback(null, resultado.data);
    })
    .catch((err) => {
      callback(err);
    });
};

// getUsuarios
// Busca a lista de usuÃ¡rios cadastrados.
const getListaUsuarios = (callback) => {
  Client.get('/usuario')
    .then((resultado) => {
      callback(null, resultado.data);
    })
    .catch((err) => {
      callback(err);
    });
};

const novaSenha = (senha, senhaNova, callback) => {
  let body = { senha, senhaNova };

  Client.post('/usuario/alterar-senha', body)
    .then((resultado) => {
      resultado;
      callback(null, true);
    })
    .catch((err) => {
      callback(err);
    });
};

const novoUsuario = (usuario, callback) => {
  let body = { usuario };
  Client.post('/usuario', body)
    .then((resultado) => {
      callback(null, resultado);
    })
    .catch((err) => {
      callback(err);
    });
};

